@mixin gradient-darkblue()
    background: linear-gradient(135deg, #0078d4, #0078d4bd, #0078d4)

@mixin gradient-lightblue()
    background: linear-gradient(135deg, #00aed4, #00aed4b0, #00aed4)

@mixin gradient-turqoise()
    background: linear-gradient(135deg, #47968a, #47968ad2, #47968a)

@mixin gradient-darkgrey()
    background: linear-gradient(135deg, #444444, #444444c7, #444444)

@mixin gradient-yellowgreen()
    background: linear-gradient(135deg, #88c057, #88c057c5, #88c057)

@mixin gradient-red()
    background: linear-gradient(135deg, #e22500, #e22500bf, #e22500)
